
const headerBtn = document.querySelector('.header__top-btn');
const consultBtn = document.querySelector('.main__btn');

const crossCall = document.querySelector('.form__cross-call');
const crossConsult = document.querySelector('.form__cross-consult');

const modalBodyCall = document.querySelector('._body-call');
const modalBodyConsult = document.querySelector('._body-consult');

const form = document.querySelectorAll('.form');

const wrp = document.querySelector('.wrp');
const scrollOffset = (window.innerWidth - wrp.offsetWidth) + 'px';
const fixBlock = document.querySelectorAll('.fix-block');


function modal(btn, cross, modal) {
   btn.addEventListener('click', () => {
        modal.classList.add('active');
        document.body.style.overflow = 'hidden';

        form.forEach(item => {
            item.classList.add('active');
        })

        document.body.style.paddingRight = scrollOffset;
        fixBlock.forEach(item => {
            item.style.paddingRight = scrollOffset;
        })
    })
    modal.addEventListener('click', (e) => {
        if(!e.target.closest('.form')) {
            modal.classList.remove('active');
            document.body.style.overflow = '';

            form.forEach(item => {
                item.classList.remove('active');
            })

            document.body.style.paddingRight = 0 + 'px';
            fixBlock.forEach(item => {
                item.style.paddingRight = 0 + 'px';
            })
        }
    })
    cross.addEventListener('click', () => {
        modal.classList.remove('active');
        document.body.style.overflow = '';

        form.forEach(item => {
            item.classList.remove('active');
        })

        document.body.style.paddingRight = 0 + 'px';
        fixBlock.forEach(item => {
            item.style.paddingRight = 0 + 'px';
        })
    })
}

modal(headerBtn, crossCall, modalBodyCall);
modal(consultBtn, crossConsult, modalBodyConsult);

