
//Прилипание шапки по скроллу
const topMenu = document.querySelector('.header__top-menu');
const bottomMenu = document.querySelector('.header__bottom-menu');
const headerLogo = document.querySelector('.header__logo');
const menu = document.querySelector('.menu');
const locMobile = document.querySelector('.loc-mob');

window.addEventListener('scroll', () => {
    if(window.scrollY >= topMenu.clientHeight) {
       bottomMenu.classList.add('active');
    } else {
        bottomMenu.classList.remove('active');
    }
})




